import { BtnClose } from '../../../lib/components/btn-close/btn-close';
import { PopupStandaloneModalComp } from '../../../lib/components/popups/standalone/modal/popup-standalone-modal';

import styles from './popup-list.scss';
import sharedStyles from '../../../styles/styles.scss';

export type PopupStandaloneModalListTextContent = {
  HEADER: string;
  P1: string;
  OL: Record<string, LiItem>;
  CAPTION: string;
};

export type LiItem = {
  text?: string;
  anchors?: LiItemAnchor[];
};

export type LiItemAnchor = {
  href: string;
  text: string;
  target: '_blank';
};

export abstract class PopupStandaloneModalList extends PopupStandaloneModalComp {
  protected TEXT_CONTENT: PopupStandaloneModalListTextContent;

  protected list = document.createElement('ol');

  protected btnOk = document.createElement('button');

  protected btnCloseContainer = document.createElement('div');

  protected caption = document.createElement('p');

  constructor(TEXT_CONTENT: PopupStandaloneModalListTextContent) {
    super();
    this.TEXT_CONTENT = TEXT_CONTENT;
    this.header.textContent = this.TEXT_CONTENT.HEADER;

    this.renderList();

    this.renderCloseBtns();
    this.setupCloseActions();

    this.renderOtherThings();

    this.body.classList.add(styles.body);
    this.body.append(
      this.list,
      this.btnCloseContainer,
      this.caption,
      this.btnOk,
    );
  }

  private renderCloseBtns() {
    const btnCloseComp = new BtnClose();
    this.btnCloseContainer.classList.add(styles['btn-close-container']);
    btnCloseComp.rootElement.classList.add(styles['btn-close']);
    this.btnCloseContainer.append(btnCloseComp.rootElement);

    this.btnOk.classList.add(
      sharedStyles.btn,
      sharedStyles['btn-6'],
      styles['btn-ok'],
    );

    const btnOkOuterSpan = document.createElement('span');
    const btnOkInnerSpan = document.createElement('span');
    btnOkInnerSpan.textContent = 'OK';
    btnOkInnerSpan.dataset.textContent = 'OK';
    btnOkInnerSpan.classList.add(styles['btn-ok__innerSpan']);
    btnOkOuterSpan.append(btnOkInnerSpan);
    this.btnOk.append(btnOkOuterSpan);
  }

  private renderList() {
    const liItemsArray = Object.values(this.TEXT_CONTENT.OL);
    const liArray = liItemsArray.map(liItem => {
      const li = document.createElement('li');

      if (liItem.text) {
        const span = document.createElement('span');
        span.textContent = liItem.text;
        li.append(span);
      }

      const anchors = liItem.anchors?.map(anchor => {
        const anchorElem = document.createElement('a');
        anchorElem.textContent = anchor.text;
        anchorElem.href = anchor.href;
        anchorElem.target = anchor.target;
        return anchorElem;
      });

      if (anchors) li.append(...anchors);

      return li;
    });
    this.list.append(...liArray);
    this.list.classList.add(styles.ol);
  }

  private renderOtherThings() {
    this.caption.textContent = this.TEXT_CONTENT.CAPTION;
  }

  private setupCloseActions() {
    this.btnCloseContainer.addEventListener('click', () => this.hideSmoothly());
    this.btnOk.addEventListener('click', () => this.hideSmoothly());
    document.body.addEventListener('keydown', e => {
      if (e.key === 'Escape') this.hideSmoothly();
    });
  }
}
