import { DBusMsgRoutingMsg } from '../dbus-msg-routing-msg.def';

export type RoutingRestrainActions = 'restrain' | 'unleash';

export class DBusMsgRoutingRestrainMsg extends DBusMsgRoutingMsg {
  constructor(
    readonly action: RoutingRestrainActions,
    readonly callback: () => Promise<boolean>,
    rxIds?: string[],
    prefResponseRxIds?: string[],
  ) {
    super(rxIds, prefResponseRxIds);
  }
}
