import { PopupStandaloneModalChoiceComp } from '../../../lib/components/popups/standalone/modal/choice/popup-choice';

import styles from './popup-choice-first-open.scss';
import sharedStyles from '../../../styles/styles.scss';

export type FirstOpenDesicions = 'disable glitch' | 'enable glitch';

export class PopupStandaloneModalChoiceFirstOpenComp extends PopupStandaloneModalChoiceComp {
  static readonly TEXT_CONTENT_ENG = {
    HEADER: 'Welcome to ASYNC RACE by no95typem',
    TEXT: `Hey! Before starting,
      I ask you to restart the server
      because I cannot predict what was done with it before me!
      In addition, the application seems to be overloaded with effects,
      some of which can be dangerous for people with epilepsy.
      So choose whether to disable them or not.
      And please read the notes on the "Winners" page.`,
  };

  private TEXT_CONTENT =
    PopupStandaloneModalChoiceFirstOpenComp.TEXT_CONTENT_ENG;

  constructor() {
    super();
    this.body.classList.add(styles.body);
    this.renderText();

    const enableBtn = document.createElement('button');
    const outerSpan = document.createElement('span');
    const innerSpan = document.createElement('span');
    innerSpan.textContent = 'enable glitch';
    innerSpan.dataset.textContent = 'enable glitch';
    outerSpan.append(innerSpan);
    enableBtn.append(outerSpan);
    innerSpan.classList.add(styles.glitch);
    enableBtn.classList.add(styles['glitch-parent']);

    const disableBtn = document.createElement('button');
    const span = document.createElement('span');
    span.textContent = 'disable glitch';
    disableBtn.append(span);

    this.btns.push(enableBtn, disableBtn);
    this.mapBtnsAndChoices(
      { btn: enableBtn, desicion: 'enable glitch' },
      { btn: disableBtn, desicion: 'disable glitch' },
    );

    this.btns.forEach(btn => {
      btn.classList.add(sharedStyles.btn, sharedStyles['btn-6'], styles.btn);
    });

    this.btnBlock.append(...this.btns);

    this.btnBlock.classList.add(styles['btn-block']);
  }

  private renderText() {
    this.header.textContent = this.TEXT_CONTENT.HEADER;
    this.text.textContent = this.TEXT_CONTENT.TEXT;
  }
}
