import { commitStyles } from '../../../../gui/animation';
import { PopupStandaloneComp } from '../popup-standalone';

import styles from './popup-standalone-modal.scss';

export abstract class PopupStandaloneModalComp extends PopupStandaloneComp {
  constructor() {
    super();
    this._rootElement.classList.add(styles['popup-modal']);
    const closeArea = document.createElement('div');
    closeArea.classList.add(styles['popup-bg']);
    this._rootElement.append(closeArea);
    this.body.classList.add(styles['popup-body']);
    closeArea.append(this.body);
    this.header.classList.add(styles['popup-body-header']);
    this.text.classList.add(styles['popup-body-text']);
  }

  setHeader(text: string): PopupStandaloneModalComp {
    this.header.textContent = text;
    this.header.dataset.textContent = text;
    return this;
  }

  setText(text: string): PopupStandaloneModalComp {
    this.text.textContent = text;
    this.text.dataset.textContent = text;
    return this;
  }

  show(rootElem?: HTMLElement): PopupStandaloneModalComp {
    const root = rootElem || document.body;
    root.append(this._rootElement);
    return this;
  }

  showSmoothly(rootElem?: HTMLElement): PopupStandaloneModalComp {
    this.show(rootElem);
    const anim = this._rootElement.animate(
      [{ opacity: `0` }, { opacity: `1` }],
      {
        duration: 500,
        easing: 'ease-in-out',
        fill: 'forwards',
      },
    );
    anim.oncancel = () => commitStyles(anim);
    anim.onfinish = () => commitStyles(anim);
    return this;
  }

  hide(): PopupStandaloneModalComp {
    this._rootElement.remove();
    return this;
  }

  async hideSmoothly(): Promise<PopupStandaloneModalComp> {
    const anim = this._rootElement.animate(
      [{ opacity: `1` }, { opacity: `0` }],
      {
        duration: 500,
        easing: 'ease-in-out',
        fill: 'forwards',
      },
    );
    await anim.finished;
    this.hide();
    commitStyles(anim);
    this._rootElement.style.removeProperty('opacity');
    return this;
  }
}
