export class Pager {
  private firstPageNumber = 1;

  private currPageNumber = this.firstPageNumber;

  private lastPageNumber = 0;

  private pagerSpan = document.createElement('span');

  private entriesCount = 0;

  private callbackIsCalled = false;

  constructor(
    private entriesPerPage: number,
    protected readonly listCallback: (dir: 'prev' | 'next') => Promise<boolean>,
    private btnPrevPage = document.createElement('button'),
    private btnNextPage = document.createElement('button'),
  ) {
    const btnNextSpan = document.createElement('span');
    btnNextSpan.textContent = 'Next page';
    this.btnNextPage.append(btnNextSpan);

    const btnPrevSpan = document.createElement('span');
    btnPrevSpan.textContent = 'Prev page';
    this.btnPrevPage.append(btnPrevSpan);

    this.btnNextPage.addEventListener('click', () => this.callCallback('next'));
    this.btnPrevPage.addEventListener('click', () => this.callCallback('prev'));
  }

  public toogleLock(lock: boolean): void {
    this.toggleBtnsDisabled(lock);
  }

  public getEntriesPerPage(): number {
    return this.entriesPerPage;
  }

  public setLastPageNumber(n: number): void {
    this.lastPageNumber = n;
    if (!this.callbackIsCalled) this.toggleBtnsDisabled(false);
  }

  public setEntriesCount(n: number): void {
    this.entriesCount = n;
    this.lastPageNumber = Math.ceil(this.entriesCount / this.entriesPerPage);
    this.updateSpanText();
    if (!this.callbackIsCalled) this.toggleBtnsDisabled(false);
  }

  public getCurrPageNumber(): number {
    return this.currPageNumber;
  }

  public getBtnPrevPage(): HTMLSpanElement {
    return this.btnPrevPage;
  }

  public getBtnNextPage(): HTMLSpanElement {
    return this.btnNextPage;
  }

  public getPagerSpan(): HTMLSpanElement {
    return this.pagerSpan;
  }

  tryChangePage(dir: 'prev' | 'next'): Promise<void> {
    return this.callCallback(dir);
  }

  private updateSpanText() {
    const pageNumber = this.entriesCount > 0 ? this.currPageNumber : 0;
    this.pagerSpan.textContent = `Entries: ${this.entriesCount}, Page: ${pageNumber}/${this.lastPageNumber}`;
  }

  private async callCallback(dir: 'prev' | 'next') {
    if (this.callbackIsCalled) return;
    this.callbackIsCalled = true;
    this.toggleBtnsDisabled(true);
    this.listCallback(dir)
      .then(pageIsChanged => {
        if (pageIsChanged) {
          switch (dir) {
            case 'next':
              this.currPageNumber++;
              break;
            case 'prev':
              this.currPageNumber--;
              break;
            default:
              break;
          }
          this.updateSpanText();
        }
      })
      .finally(() => {
        this.toggleBtnsDisabled(false);
        this.callbackIsCalled = false;
      });
  }

  private toggleBtnsDisabled(state: boolean) {
    if (state === true) {
      this.btnPrevPage.disabled = true;
      this.btnNextPage.disabled = true;
      return;
    }

    if (this.currPageNumber > this.firstPageNumber)
      this.btnPrevPage.disabled = false;
    else this.btnPrevPage.disabled = true;

    if (this.currPageNumber < this.lastPageNumber)
      this.btnNextPage.disabled = false;
    else this.btnNextPage.disabled = true;
  }
}
