import { OBJ_PROCESSOR } from '../../../lib/processors/obj-processor';
import { RestClient } from '../rest-client';
import { REST_CLIENT_ERROR } from '../rest-clients-errors.def';

export type EngineResponse = {
  velocity: number;
  distance: number;
};

class RestClientEngine extends RestClient {
  constructor(baseUrl: string) {
    super(baseUrl, '/engine');
  }

  toggleEngine(
    id: number,
    status: 'started' | 'stopped',
  ): Promise<EngineResponse> {
    return new Promise((res, rej) => {
      const url = `${this.fullUrl}?id=${id}&status=${status}`;
      fetch(url, {
        method: 'GET',
      })
        .then(response => {
          if (response.status === 200) return response.json();
          throw new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT);
        })
        .then(json => res(json as EngineResponse))
        .catch((error: Error) => RestClient.handleError(error, rej));
    });
  }

  enterDriveMode(id: number): Promise<true | false> {
    return new Promise((res, rej) => {
      const url = `${this.fullUrl}?id=${id}&status=drive`;
      fetch(url, {
        method: 'GET',
      })
        .then(response => {
          if (response.status === 200) res(true);
          else if (response.status === 500) res(false);
          else rej(new Error(REST_CLIENT_ERROR.UNEXPECTED_RESULT));
        })
        .catch((error: Error) => RestClient.handleError(error, rej));
    });
  }
}

const obj = new RestClientEngine('http://127.0.0.1:3000');

const REST_CLIENT_ENGINE = OBJ_PROCESSOR.deepFreeze(obj);

export { REST_CLIENT_ENGINE };
