import { RegCarData, WinnerCarData } from '../../definitions/car-data.def';
import { DBusInterface } from '../../lib/services/dbus/defs/dbus-interface.def';
import { DBusPort } from '../../lib/services/dbus/defs/dbus-port.def';
import {
  DBusTXPrepModes,
  DBusTXWaitModes,
} from '../../lib/services/dbus/defs/dbus-tx/dbus-tx-modes.def';
import { DBusTX } from '../../lib/services/dbus/defs/dbus-tx/dbus-tx.def';
import { DBusMsgCarActionSelectedMsg } from './dbus-msgs/by-topic/car-action/garage-car-select-msg.def';
import { REST_CLIENT_GARAGE } from '../../services/rest-clients/client-garage/rest-client-garage';
import { DBusMsgCarActionRemovedMsg } from './dbus-msgs/by-topic/car-action/garage-car-remove-msg.def';
import { GarageServicemanContract } from './garage-serviceman-contract.def';
import { GarageDriver } from './garage-driver';
import { genUniqId } from '../../lib/generators/generators';

import styles from './garage.scss';
import { REST_CLIENT_WINNERS } from '../../services/rest-clients/client-winners/rest-client-winners';
import { REST_CLIENT_ERROR } from '../../services/rest-clients/rest-clients-errors.def';
import { DBusMsgGarageRaceStatusMsg } from './dbus-msgs/by-topic/garage/garage-race-status-msg.def';
import { RaceStatusesList } from './race-statuses.def';
import { getTransitionDuration } from '../../lib/gui/animation';
import { GaragePopupWinner } from './subcomps/popup-winner/popup-winner';
import {
  DBusMsgRoutingRestrainMsg,
  RoutingRestrainActions,
} from '../../lib/services/simple-router/dbus-msgs-defs/types/routing-restrict-msg.def';
import {
  DBusMsgGarageStatusMsg,
  GarageStatus,
} from './dbus-msgs/by-topic/garage/garage-status-msg.def';

type RaceData = {
  raceId: string;
  arrivedByOrder: GarageServicemanContract[];
  notArrived: GarageServicemanContract[];
  participantsCount: number;
  flags: Record<string, unknown>;
  stratedCount: number;
};

export class GarageManagersTeam implements DBusPort {
  protected txs = new Set<DBusTX>();

  protected contracts = new Set<GarageServicemanContract>();

  protected freeDrivers = new Set<GarageDriver>();

  protected busyDrivers = new Map<GarageServicemanContract, GarageDriver>();

  protected movingDrivers = new Map<GarageServicemanContract, GarageDriver>();

  protected currRaceId: string | undefined;

  protected winnerPopup = new GaragePopupWinner();

  protected rollbackPromise: Promise<unknown> | undefined;

  constructor(
    private readonly tracksContainer: HTMLElement,
    private readonly carCtlsBlock: HTMLElement,
    private readonly directorLockCb: (state: boolean) => Promise<boolean>,
  ) {
    this.carCtlsBlock.addEventListener('click', this.carCtlsClickListener);
    this.winnerPopup.rootElement.classList.add(styles['garage__winner-popup']);
  }

  autoConnect(int: DBusInterface): void {
    const tx = int.getNewTX('garage serviceman');
    this.txs.add(tx);
  }

  public getContracts(): Set<GarageServicemanContract> {
    return this.contracts;
  }

  public handleCarRm(car: RegCarData): void {
    this.contracts.forEach(contract => {
      if (contract.car.currCar.id === car.id)
        this.busyDrivers.get(contract)?.getOutOfTheCar();
    });
  }

  async nullifyContracts(
    contracts?: GarageServicemanContract[],
  ): Promise<void> {
    if (contracts) {
      this.tryFreeDrivers(...contracts);
      contracts.forEach(contract => {
        this.contracts.delete(contract);
      });
    } else {
      this.tryFreeDrivers(...this.contracts);
      this.contracts.clear();
    }
  }

  addContracts(...сontracts: GarageServicemanContract[]): void {
    сontracts.forEach(newContract => this.contracts.add(newContract));
  }

  private carCtlsClickListener = (e: Event) => {
    if (e.target instanceof HTMLElement) {
      const elem = e.target;
      let btn: HTMLButtonElement | undefined;

      if (elem instanceof HTMLButtonElement) btn = elem;
      else if (elem.parentElement instanceof HTMLButtonElement)
        btn = elem.parentElement;

      if (btn && btn.disabled !== true) {
        this.contracts.forEach(contract => {
          if (this.currRaceId) return;
          if (btn === contract.car.btnSelect) {
            this.handleSelection(contract);
            return;
          }
          if (btn === contract.car.btnRm) {
            contract.car.btnRm.disabled = true;
            this.handleRemoving(contract);
            return;
          }
          if (btn === contract.car.btnStart) {
            contract.car.btnStart.disabled = true;
            this.startDriving(contract);
            return;
          }
          if (btn === contract.car.btnReset) {
            contract.car.btnReset.disabled = true;
            this.handleRestoreState(contract);
          }
        });
      }
    }
  };

  async resetState(prepareForRace?: boolean): Promise<void> {
    if (!prepareForRace) this.currRaceId = undefined;
    this.winnerPopup.rootElement.remove();
    this.tracksContainer.classList.remove(
      styles['synthwave-background_race-mode'],
    );
    await this.rollbackCars();
    this.broadcastGarageStatus('reset is not needed');

    if (!prepareForRace) {
      this.broadcastRaceStatus('READY_TO_GO');
    }
  }

  private async handleRestoreState(
    contract: GarageServicemanContract,
  ): Promise<void> {
    const driver = this.busyDrivers.get(contract);
    if (driver) {
      await driver.returnToStart();
      await this.broadcastIfAllInStart();
    }
  }

  private async broadcastIfAllInStart(): Promise<void> {
    const allInStart = [...this.busyDrivers].every(contract =>
      contract[1].isInStartPosition(),
    );
    if (allInStart) {
      this.broadcastGarageStatus('reset is not needed');
    }
  }

  private async startDriving(
    contract: GarageServicemanContract,
    flags?: Record<string, unknown>,
  ) {
    let choosedDriver = this.busyDrivers.get(contract);

    if (!choosedDriver) {
      // !!! excess check?
      choosedDriver = [...this.freeDrivers].find(driver => driver.isFree());

      if (choosedDriver) {
        this.freeDrivers.delete(choosedDriver);
      } else {
        choosedDriver = new GarageDriver();
      }

      this.busyDrivers.set(contract, choosedDriver);

      choosedDriver.putBehindTheWheel(contract);
    }

    this.movingDrivers.set(contract, choosedDriver);
    if (this.movingDrivers.size === 1) {
      await this.broadcastGarageStatus('need reset');
      await this.toggleRoutingRestrains('restrain');
      await this.broadcastGarageStatus('car moves');
    }

    return choosedDriver
      .performDrivingProtocol(flags)
      .then(response => {
        response.drivePromise.finally(() => {
          this.movingDrivers.delete(contract);

          if (this.movingDrivers.size === 0) {
            this.toggleRoutingRestrains('unleash');
            this.broadcastGarageStatus('all cars stopped');
          }
        });
        return response;
      })
      .catch(error => {
        this.movingDrivers.delete(contract);

        if (this.movingDrivers.size === 0) {
          this.toggleRoutingRestrains('unleash');
          this.broadcastGarageStatus('all cars stopped');
        }

        throw error;
      });
  }

  private async broadcastGarageStatus(status: GarageStatus) {
    const msg = new DBusMsgGarageStatusMsg(status);
    const transmissions: Promise<unknown>[] = [];
    this.txs.forEach(tx => {
      const x = tx.transmit({
        msg,
        prepMode: DBusTXPrepModes.freeze,
        waitMode: DBusTXWaitModes.allSetled,
      });
      transmissions.push(x);
    });
    return Promise.allSettled(transmissions);
  }

  toggleRoutingRestrains(action: RoutingRestrainActions): Promise<unknown> {
    const msg = new DBusMsgRoutingRestrainMsg(action, this.routingRestrainFunc);
    const transmissions: Promise<unknown>[] = [];
    this.txs.forEach(tx => {
      const x = tx.transmit({
        msg,
        prepMode: DBusTXPrepModes.freeze,
        waitMode: DBusTXWaitModes.allSetled,
      });
      transmissions.push(x);
    });
    return Promise.allSettled(transmissions);
  }

  private routingRestrainFunc = async () => {
    return false;
  };

  private handleRemoving(contract: GarageServicemanContract) {
    REST_CLIENT_GARAGE.deleteCar(contract.car.currCar.id).then(async () => {
      await REST_CLIENT_WINNERS.deleteWinner(contract.car.currCar.id).catch(
        () => {},
      );
      this.tryFreeDrivers(contract);
      this.contracts.delete(contract);
      const msg = new DBusMsgCarActionRemovedMsg(
        contract.car.currCar,
        contract,
      );
      this.txs.forEach(tx => {
        tx.transmit({
          msg,
          prepMode: DBusTXPrepModes.freeze,
          waitMode: DBusTXWaitModes.nowait,
        });
      });
    });
  }

  private handleSelection(contract: GarageServicemanContract) {
    const msg = new DBusMsgCarActionSelectedMsg(
      contract.car.currCar,
      this.genSelectCallback(contract),
    );
    this.txs.forEach(tx => {
      tx.transmit({
        msg,
        prepMode: DBusTXPrepModes.freeze,
        waitMode: DBusTXWaitModes.nowait,
      });
    });
  }

  private async rollbackCars() {
    if (this.rollbackPromise) {
      await this.rollbackPromise;
      return;
    }
    this.tracksContainer.classList.remove(
      styles['garage__track-container_finish'],
    );
    this.tracksContainer.style.setProperty('--translate-x', '0px');
    // rollback cars
    const rollbacks: Promise<unknown>[] = [];
    this.busyDrivers.forEach(driver => {
      rollbacks.push(driver.returnToStart());
    });
    this.rollbackPromise = Promise.all(rollbacks).finally(() => {
      this.rollbackPromise = undefined;
    });
    await this.rollbackPromise;
  }

  async performRace(): Promise<void> {
    // prepare
    const thisRaceId = genUniqId();
    this.currRaceId = thisRaceId;
    await this.resetState(true);
    // check if user cancell a race
    if (thisRaceId !== this.currRaceId) return;

    // process race
    const raceData: RaceData = {
      raceId: thisRaceId,
      arrivedByOrder: [],
      notArrived: [],
      participantsCount: this.contracts.size,
      flags: {
        roadElem: this.tracksContainer,
        isFinishVisible: false, // TODO: rm
      },
      stratedCount: 0,
    };
    this.broadcastRaceStatus('IN_PROCESS');
    await this.processRace(raceData)
      .catch((error: Error) => {
        if (!(thisRaceId !== this.currRaceId)) throw error;
      })
      .finally(() => {
        this.tracksContainer.classList.remove(
          styles['synthwave-background_race-mode'],
        );
      });

    // check if user cancell a race
    if (thisRaceId !== this.currRaceId) return;

    // console.log(raceData);

    this.broadcastRaceStatus('NEED_RESET');
    // TODO: Do something else?
  }

  private broadcastRaceStatus(status: RaceStatusesList) {
    const msg = new DBusMsgGarageRaceStatusMsg(status);
    this.txs.forEach(tx => {
      tx.transmit({
        msg,
        prepMode: DBusTXPrepModes.freeze,
        waitMode: DBusTXWaitModes.nowait,
      });
    });
  }

  // false if race was interrupted
  private processRace(raceData: RaceData) {
    return new Promise<RaceData | false>(res => {
      this.contracts.forEach(contract => {
        this.startDriving(contract, raceData.flags).then(response => {
          if (raceData.raceId !== this.currRaceId) {
            res(false);
            return;
          }

          if (raceData.stratedCount === raceData.participantsCount - 1) {
            this.tracksContainer.classList.add(
              styles['synthwave-background_race-mode'],
            );
            raceData.flags.isRoadMoves = true;
          } else raceData.stratedCount++;

          if (!(response instanceof Error)) {
            response.drivePromise
              .then(result =>
                this.handleDriveResult(raceData, contract, result),
              )
              .catch((error: Error) => {
                if (error.message === 'interrupted') res(false);
              })
              .finally(() => {
                if (
                  raceData.arrivedByOrder.length +
                    raceData.notArrived.length ===
                  raceData.participantsCount
                ) {
                  res(raceData);
                }
              });
          } else {
            throw response;
          }
        });
      });
    });
  }

  private handleDriveResult(
    raceData: RaceData,
    contract: GarageServicemanContract,
    result: number | false,
  ) {
    if (raceData.raceId !== this.currRaceId) return;
    if (result !== false) {
      if (raceData.arrivedByOrder.length === 0) {
        this.handleWinner(contract, result, raceData);
      }
      raceData.arrivedByOrder.push(contract);
    } else {
      raceData.notArrived.push(contract);
    }
  }

  private handleWinner = async (
    contract: GarageServicemanContract,
    time: number,
    raceData: RaceData,
  ) => {
    raceData.flags.isFinishVisible = true;
    this.tracksContainer.classList.add(
      styles['garage__track-container_finish'],
    );
    const style = window.getComputedStyle(this.tracksContainer, '::after');
    const duration = getTransitionDuration(style, 'right');
    if (duration) {
      const awaitTime = Number.parseFloat(duration);
      if (awaitTime > 0)
        await new Promise(res => setTimeout(res, awaitTime * 1000));
    }
    this.tracksContainer.classList.remove(
      styles['synthwave-background_race-mode'],
    );
    raceData.flags.isRoadMoves = false;

    if (this.currRaceId !== raceData.raceId) return;

    const winnerCar = contract.car.currCar;
    GarageManagersTeam.saveWinner(winnerCar, time);
    this.winnerPopup.setData(
      winnerCar,
      time.toFixed(2),
      contract.car.index?.toString(),
    );
    this.tracksContainer.append(this.winnerPopup.rootElement);
  };

  static saveWinner(winnerCar: RegCarData, time: number): void {
    REST_CLIENT_WINNERS.getWinner(winnerCar.id)
      .then(winnerRecord => {
        const updatedWinnerData: WinnerCarData = {
          id: winnerRecord.id,
          wins: winnerRecord.wins + 1,
          time:
            time < winnerRecord.time
              ? Number.parseFloat(time.toFixed(2))
              : winnerRecord.time,
        };
        REST_CLIENT_WINNERS.updateWinner(updatedWinnerData);
      })
      .catch((error: Error) => {
        if (error.message === REST_CLIENT_ERROR.NOT_FOUND) {
          const newWinnerData: WinnerCarData = {
            id: winnerCar.id,
            wins: 1,
            time: Number.parseFloat(time.toFixed(2)),
          };
          REST_CLIENT_WINNERS.createWinner(newWinnerData);
        }
      });
  }

  private tryFreeDrivers(...contracts: GarageServicemanContract[]) {
    contracts.forEach(contract => {
      const driver = this.busyDrivers.get(contract);
      if (driver) {
        driver.getOutOfTheCar().then(() => {
          this.busyDrivers.delete(contract);
          this.freeDrivers.add(driver);
        });
      }
    });
  }

  private genSelectCallback(contract: GarageServicemanContract) {
    return (car: RegCarData) => {
      if (!this.contracts.has(contract)) return;
      contract.car.updateData(car);
    };
  }
}
