import { DBusMsgGarageMsg } from './garage-msg.def';

export type GarageStatus =
  | 'need reset'
  | 'car moves'
  | 'all cars stopped'
  | 'reset is not needed';

export class DBusMsgGarageStatusMsg extends DBusMsgGarageMsg {
  constructor(
    readonly status: GarageStatus,
    rxIds?: string[],
    prefResponseRxIds?: string[],
  ) {
    super(rxIds, prefResponseRxIds);
  }
}
