import { REST_CLIENT_GARAGE } from '../../services/rest-clients/client-garage/rest-client-garage';
import { REST_CLIENT_WINNERS } from '../../services/rest-clients/client-winners/rest-client-winners';
import {
  RestClientWinnersSortOptionsList,
  REST_CLIENT_WINNERS_SORT_OPTIONS,
} from '../../services/rest-clients/client-winners/rest-client-winners.def';
import { WinnerRowComp } from './subcomps/winner-entry';
import { Pager } from '../../lib/services/pager/pager';

import styles from './winners.scss';
import sharedStyles from '../../styles/styles.scss';
import { Notifiable } from '../../lib/interfaces/notifiable';
import {
  DBusHub,
  DBusInterface,
} from '../../lib/services/dbus/defs/dbus-interface.def';
import { DBUS_HUB_NAMES } from '../../lib/services/dbus/defs/dbus-hub-names.def';
import { DBusTX } from '../../lib/services/dbus/defs/dbus-tx/dbus-tx.def';
import { DBusMsgAppCtlReconfureReqMsg } from '../../app/dbus-msgs/reconfigure-req.def';
import {
  DBusTXPrepModes,
  DBusTXWaitModes,
} from '../../lib/services/dbus/defs/dbus-tx/dbus-tx-modes.def';
import { PopupStandaloneModalProgressComp } from '../../lib/components/popups/standalone/modal/progress/popup-progress';
import { getRandomInt } from '../../lib/generators/generators';
import { PopupStandaloneModalListNotes } from '../popups/popup-list/inhereitors/popup-notes/popup-notes';
import { PopupStandaloneModalListCredits } from '../popups/popup-list/inhereitors/popup-credits/popup-credits';
import { WinnerCarData } from '../../definitions/car-data.def';

type BGInfo = {
  src: string;
  styles: string;
};

export class WinnersComp extends BaseComp implements DBusHub, Notifiable {
  static readonly TEXT_CONTENT_ENG = {};

  static readonly BG_SRCS: BGInfo[] = [
    {
      src: './assets/imgs/backgrounds/winners/alessandro-aramino-artboard-1.jpg',
      styles: 'no-repeat left 50% top 0% / cover',
    },
    {
      src: './assets/imgs/backgrounds/winners/alexander-dudar-q000-chase7-06.jpg',
      styles: 'no-repeat right 0% top 0% / cover',
    },
    {
      src: './assets/imgs/backgrounds/winners/alexander-dudar-q000-chase8.jpg',
      styles: 'no-repeat right 0% top 0% / cover',
    },
    {
      src: './assets/imgs/backgrounds/winners/andrzej-bury-1-9.jpg',
      styles: '',
    },
    {
      src: './assets/imgs/backgrounds/winners/andrzej-bury-1-12.jpg',
      styles: '',
    },
    {
      src: './assets/imgs/backgrounds/winners/charlotte-hughes-18.jpg',
      styles: '',
    },
    {
      src: './assets/imgs/backgrounds/winners/lukasz-sprada-18.jpg',
      styles: 'no-repeat right 0% top 0% / cover',
    },
    {
      src: './assets/imgs/backgrounds/winners/masahiro-sawada-arasaka-warehouse.jpg',
      styles: '',
    },
    {
      src: './assets/imgs/backgrounds/winners/ward-lindhout-charter-hill-highway2.jpg',
      styles: 'no-repeat right 0% bottom 0% / cover',
    },
    {
      src: './assets/imgs/backgrounds/winners/wiktoria-stachowska-ow-05.jpg',
      styles: '',
    },
    {
      src: './assets/imgs/backgrounds/winners/witold-justyniak-04-v19a.jpg',
      styles: '',
    },
  ];

  readonly hubName = DBUS_HUB_NAMES.WINNERS;

  private table = document.createElement('table');

  private tableBody = document.createElement('tbody');

  private tableHead = document.createElement('thead');

  private thId = document.createElement('th');

  private thCar = document.createElement('th');

  private thName = document.createElement('th');

  private thWins = document.createElement('th');

  private thBestTime = document.createElement('th');

  private pager: Pager;

  private pagerBtnsContainer = document.createElement('div');

  private pagerSpan: HTMLSpanElement;

  private currSortedElem: HTMLElement | undefined;

  private currOrder: 'ASC' | 'DESC' = 'ASC';

  private currSort: RestClientWinnersSortOptionsList = 'time';

  private entriesPerPage = 10;

  protected txs = new Set<DBusTX>();

  protected bottomBtnsContainer = document.createElement('div');

  protected btnReconfigureGlitch = document.createElement('button');

  protected btnNotes = document.createElement('button');

  protected btnCredits = document.createElement('button');

  protected popupNotes = new PopupStandaloneModalListNotes();

  protected popupCredits = new PopupStandaloneModalListCredits();

  protected currBGInfo: BGInfo | undefined;

  protected popup = new PopupStandaloneModalProgressComp();

  // * good
  constructor() {
    super('section');

    this.pager = new Pager(this.entriesPerPage, this.pagerCallback);
    this.pagerSpan = this.pager.getPagerSpan();

    this.start();
  }

  // * good
  private async start() {
    await this.loadBg();

    this.renderPager();

    this.renderTable();

    this.renderBottomBtnsContainer();

    this.setupBottomBtnsActions();

    this._rootElement.classList.add(styles.winners);
    this._rootElement.append(this.table, this.bottomBtnsContainer);
  }

  // * good
  static getRandomBGInfo(currBGInfo?: BGInfo): BGInfo {
    const randomIndex = getRandomInt(WinnersComp.BG_SRCS.length);
    const bgInfo = WinnersComp.BG_SRCS[randomIndex] as BGInfo;

    if (currBGInfo === bgInfo) return this.getRandomBGInfo(currBGInfo);

    return bgInfo;
  }

  // * good
  loadBg(): Promise<boolean> {
    return new Promise<boolean>(res => {
      const bgInfo = WinnersComp.getRandomBGInfo(this.currBGInfo);
      this.currBGInfo = bgInfo;
      const { src } = bgInfo;

      this.rootElement.style.setProperty(
        'background',
        `url('${src}') ${bgInfo.styles}`,
      );

      this.popup.reset().setText('Loading').setProgress(1).show();

      const resolve = () => {
        this.popup.setProgress(100).hide();
        res(true);
      };

      fetch(src)
        .then(async response => {
          const imgReadResponse = this.getNewImgReadResponse(
            response,
            this.popup,
          );
          imgReadResponse.blob().finally(() => {
            resolve();
          });
        })
        .catch(() => resolve());
    });
  }

  // ? NEED TO UNDERSTAND THIS IN MORE DEPTH
  // * good
  private getNewImgReadResponse = (
    response: Response,
    popup?: PopupStandaloneModalProgressComp,
  ): Response => {
    return new Response(
      new ReadableStream({
        async start(controller) {
          const length = response.headers.get('content-length');
          if (!response.body || !length) {
            controller.close();
            return;
          }
          const reader = response.body.getReader();
          const totalBytes = Number.parseInt(length, 10);
          let loadedBytes = 0;

          const readMore = async () => {
            const read = await reader.read();
            const val = read.value;

            if (val) {
              loadedBytes += val.byteLength;
              popup?.setProgress((loadedBytes / totalBytes) * 100);
              controller.enqueue(val);
            }

            if (!read.done) readMore();
            else controller.close();
          };

          readMore();
        },
      }),
    );
  };

  // * good
  private setupBottomBtnsActions() {
    this.btnNotes.addEventListener('click', () =>
      this.popupNotes.showSmoothly(),
    );
    this.btnCredits.addEventListener('click', () =>
      this.popupCredits.showSmoothly(),
    );
    this.btnReconfigureGlitch.addEventListener('click', () => {
      const msg = new DBusMsgAppCtlReconfureReqMsg();
      this.txs.forEach(tx => {
        tx.transmit({
          msg,
          prepMode: DBusTXPrepModes.freeze,
          waitMode: DBusTXWaitModes.nowait,
        });
      });
    });
  }

  // * good
  private renderBottomBtnsContainer() {
    const btnReconfigureGlitchObj = this.createBtnWithTwoSpans(
      this.btnReconfigureGlitch,
      'reconfigure glitch',
    );
    const btnNotesObj = this.createBtnWithTwoSpans(this.btnNotes, 'notes');
    const btnCreditsObj = this.createBtnWithTwoSpans(
      this.btnCredits,
      'credits',
    );

    [btnNotesObj, btnCreditsObj, btnReconfigureGlitchObj].forEach(obj => {
      obj.btn.classList.add(
        sharedStyles.btn,
        sharedStyles['btn-6'],
        styles['btn-glitch'],
      );
      obj.btnInnerSpan.classList.add(styles['btn-glitch__innerSpan']);
    });

    this.bottomBtnsContainer.classList.add(styles['bottom-btns-container']);
    this.bottomBtnsContainer.append(
      this.btnNotes,
      this.btnCredits,
      this.btnReconfigureGlitch,
    );
  }

  // * good
  private createBtnWithTwoSpans = (btn: HTMLElement, textContent: string) => {
    const btnOuterSpan = document.createElement('span');
    const btnInnerSpan = document.createElement('span');
    btnInnerSpan.textContent = textContent;
    btnInnerSpan.dataset.textContent = textContent;
    btnOuterSpan.append(btnInnerSpan);
    btn.append(btnOuterSpan);
    return {
      btn,
      btnOuterSpan,
      btnInnerSpan,
    };
  };

  // * good
  private renderTable() {
    this.renderTableHeader();
    this.table.classList.add(styles.table);
    this.table.append(
      this.tableHead,
      this.tableBody,
      this.pagerBtnsContainer,
      this.pagerSpan,
    );
  }

  // ? TEXT CONTENT MAY BE MOVED TO STATIC BUT IT'S SO SMALL...
  private renderTableHeader() {
    const tr = document.createElement('tr');

    const thIndex = document.createElement('th');
    thIndex.textContent = '№';
    this.thId.textContent = 'ID';
    this.thId.classList.add(styles['table__can-be-sorted']);
    this.thCar.textContent = 'CAR';
    this.thName.textContent = 'NAME';
    this.thWins.textContent = 'WINS';
    this.thWins.classList.add(styles['table__can-be-sorted']);
    this.thBestTime.textContent = 'BEST TIME';
    this.thBestTime.classList.add(styles['table__can-be-sorted']);

    tr.append(
      thIndex,
      this.thId,
      this.thCar,
      this.thName,
      this.thWins,
      this.thBestTime,
    );
    this.tableHead.append(tr);
    this.tableHead.addEventListener('click', this.tableHeadClickListener);
  }

  // * good
  private renderPager() {
    const btnPrevPage = this.pager.getBtnPrevPage();

    btnPrevPage.classList.add(
      sharedStyles.btn,
      sharedStyles['btn-6'],
      styles['pager-btn'],
    );

    const btnNextPage = this.pager.getBtnNextPage();

    btnNextPage.classList.add(
      sharedStyles.btn,
      sharedStyles['btn-6'],
      styles['pager-btn'],
    );

    this.pagerBtnsContainer.classList.add(styles.pager);
    this.pagerBtnsContainer.append(btnPrevPage, btnNextPage);

    this.pagerSpan.classList.add(styles['pager-span']);
  }

  // * good
  async notify(): Promise<void> {
    this.reloadData().then(() =>
      this.refreshTableHeader(this.currSortedElem || this.thBestTime),
    );
    await this.loadBg();
  }

  // * good
  private pagerCallback = async (dir: 'prev' | 'next'): Promise<boolean> => {
    try {
      await this.reloadData(dir);
      return true;
    } catch {
      return false;
    }
  };

  // * good
  private async reloadData(dir?: 'prev' | 'next'): Promise<void> {
    return new Promise((res, rej) => {
      const page = dir
        ? dir === 'next'
          ? this.pager.getCurrPageNumber() + 1
          : this.pager.getCurrPageNumber() - 1
        : this.pager.getCurrPageNumber();

      REST_CLIENT_WINNERS.getWinners(
        page,
        this.entriesPerPage,
        this.currSort,
        this.currOrder,
      ).then(async response => {
        this.pager.setEntriesCount(Number.parseInt(response.limit, 10));

        if (response.cars.length === 0) {
          if (dir) {
            rej();
            return;
          }

          if (page > 1) {
            await this.pager.tryChangePage('prev');
            res();
            return;
          }
        }

        await this.redrawWinners(response.cars, page);
        res();
      });
    });
  }

  // * good
  private async redrawWinners(cars: WinnerCarData[], pageNumber: number) {
    const rows = new Array<WinnerRowComp>(cars.length);
    const promises: Promise<unknown>[] = [];
    const indexInc = (pageNumber - 1) * 10;
    cars.forEach((winner, index) => {
      const promise = REST_CLIENT_GARAGE.getCar(winner.id).then(car => {
        const totalIndex = index + indexInc + 1;
        const row = new WinnerRowComp(totalIndex, winner, car);
        rows[index] = row;
      });
      promises.push(promise);
    });
    await Promise.allSettled(promises);
    while (this.tableBody.firstChild) this.tableBody.firstChild.remove();
    this.tableBody.append(
      ...rows.map(row => {
        row.getCarPreview().classList.add(styles['table__car-preview-box']);
        return row.rootElement;
      }),
    );
  }

  // * good
  private tableHeadClickListener = async (e: Event) => {
    let sort: RestClientWinnersSortOptionsList | undefined;
    switch (e.target) {
      case this.thId:
        sort = REST_CLIENT_WINNERS_SORT_OPTIONS.id;
        break;
      case this.thWins:
        sort = REST_CLIENT_WINNERS_SORT_OPTIONS.wins;
        break;
      case this.thBestTime:
        sort = REST_CLIENT_WINNERS_SORT_OPTIONS.time;
        break;
      default:
        break;
    }
    if (sort) {
      this.pager.toogleLock(true);
      const elem = e.target as HTMLElement;
      this.currOrder = elem.classList.contains(
        styles['table__sorted-elem_desc'],
      )
        ? 'ASC'
        : 'DESC';
      this.currSort = sort;
      await this.reloadData();
      this.refreshTableHeader(elem);
      this.pager.toogleLock(false);
    }
  };

  // * good
  private refreshTableHeader = (elem: HTMLElement) => {
    if (this.currSortedElem)
      this.currSortedElem.classList.remove(styles['table__sorted-elem']);

    elem.classList.add(styles['table__sorted-elem']);
    this.currSortedElem = elem;

    if (this.currOrder === 'ASC') {
      elem.classList.remove(styles['table__sorted-elem_desc']);
      elem.classList.add(styles['table__sorted-elem_asc']);
    } else {
      elem.classList.remove(styles['table__sorted-elem_asc']);
      elem.classList.add(styles['table__sorted-elem_desc']);
    }
  };

  // * good
  connect(int: DBusInterface): void {
    const tx = int.getNewTX('winners tx');
    this.txs.add(tx);
  }
}
