import { DBusMsgRoutingMsg } from '../dbus-msg-routing-msg.def';

export type RouterStatus = 'restrained' | 'free';

export class DBusMsgRoutingStatusMsg extends DBusMsgRoutingMsg {
  constructor(
    readonly status: RouterStatus,
    rxIds?: string[],
    prefResponseRxIds?: string[],
  ) {
    super(rxIds, prefResponseRxIds);
  }
}
