import { PopupStandaloneModalComp } from '../popup-standalone-modal';

export type PopupChoiceCtl = {
  btn: HTMLButtonElement;
  desicion: string;
};

export class PopupStandaloneModalChoiceComp extends PopupStandaloneModalComp {
  protected btnBlock = document.createElement('div');

  protected resolveFunc:
    | ((value: string | PromiseLike<string>) => void)
    | undefined;

  protected choicePromise: Promise<string>;

  protected btns: HTMLButtonElement[] = [];

  constructor() {
    super();
    this.choicePromise = new Promise<string>(res => {
      this.resolveFunc = res;
    });
    this.body.append(this.btnBlock);
  }

  recharge(): PopupStandaloneModalChoiceComp {
    this.choicePromise = new Promise<string>(res => {
      this.resolveFunc = res;
    });
    return this;
  }

  async awaitDesicion(): Promise<string> {
    return this.choicePromise;
  }

  mapBtnsAndChoices(...ctls: PopupChoiceCtl[]): void {
    ctls.forEach(control => {
      control.btn.addEventListener('click', () =>
        this.resolveFunc?.(control.desicion),
      );
    });
  }
}
