import { PopupStandaloneModalProgressComp } from '../../lib/components/popups/standalone/modal/progress/popup-progress';
import { genUniqId } from '../../lib/generators/generators';
import {
  EngineResponse,
  REST_CLIENT_ENGINE,
} from '../../services/rest-clients/client-engine/rest-client-engine';
import { REST_CLIENT_ERROR } from '../../services/rest-clients/rest-clients-errors.def';
import { GarageServicemanContract } from './garage-serviceman-contract.def';

export enum GarageDriverCommands {
  START_DRIVING,
  STOP_DRIVING,
}

export class GarageDriver {
  private contract: GarageServicemanContract | undefined;

  private currDriveId: string | undefined;

  private drivePromise: Promise<unknown> | undefined;

  private ignitionPromise: Promise<unknown> | undefined;

  private lastStart: number | undefined;

  private lastTimeInMs: number | undefined;

  public isFree(): boolean {
    if (!this.contract) return true;
    return false;
  }

  putBehindTheWheel(contract: GarageServicemanContract): void {
    if (this.contract) throw new Error('ALREADY IN CAR');
    this.contract = contract;
  }

  async getOutOfTheCar(): Promise<void> {
    this.contract = undefined;
  }

  performDrivingProtocol(flags?: Record<string, unknown>): Promise<{
    driveParams: EngineResponse;
    drivePromise: Promise<number | false>;
  }> {
    const newPromise = new Promise<{
      driveParams: EngineResponse;
      drivePromise: Promise<number | false>;
    }>((res, rej) => {
      if (flags) this.contract?.car.disableCtls(); // race
      const thisDriveId = genUniqId();
      this.currDriveId = thisDriveId;
      this.ignitionPromise = this.TurnOnTheIgnition()
        .then(driveParams => {
          if (thisDriveId !== this.currDriveId) rej(new Error('interrupted'));
          const drivePromise = this.drive(driveParams, thisDriveId, flags);
          res({
            driveParams,
            drivePromise,
          });
        })
        .catch((error: Error) => {
          if (thisDriveId !== this.currDriveId) rej(new Error('interrupted'));
          else rej(error);
        });
    });
    return newPromise;
  }

  private async TurnOnTheIgnition() {
    if (!this.contract) throw new Error('NOT_BEHIND_THE_WHEEL');
    if (this.drivePromise) await Promise.allSettled([this.drivePromise]);
    const { car } = this.contract;
    car.toggleIgniteAnim(true);
    const params = await REST_CLIENT_ENGINE.toggleEngine(
      car.currCar.id,
      'started',
    ).finally(() => {
      car.toggleIgniteAnim(false);
    });
    return params;
  }

  public isInStartPosition(): boolean {
    return !this.currDriveId;
  }

  // !!! If animation fails, this promise never resolves!
  async returnToStart(): Promise<boolean> {
    this.currDriveId = undefined;
    if (this.contract) {
      if (this.ignitionPromise) await this.ignitionPromise;
      if (this.drivePromise) {
        await this.stopCar(true, false);
        await this.contract.car.rollBack(this.drivePromise);

        const msgBlock = GarageDriver.createMsgBlock();
        this.contract.track.rootElement.append(msgBlock);
        const popup = new PopupStandaloneModalProgressComp();

        popup
          .setText(
            `I can't let the engine start again until
          the server finishes its business, read the notes, please.`,
          )
          .show(msgBlock);
        const interval = this.updateProgress(popup);
        await this.contract.car.playAwaitingAnim(this.drivePromise);
        if (interval) clearTimeout(interval);
        popup.hide();
        msgBlock.remove();
      } else {
        await this.stopCar(false, false);
        await this.contract.car.rollBack();
      }
    }
    return true;
  }

  private updateProgress(
    popup: PopupStandaloneModalProgressComp,
  ): NodeJS.Timeout {
    const interval = setInterval(() => {
      if (this.lastTimeInMs && this.lastStart) {
        const progress =
          ((Date.now() - this.lastStart) / this.lastTimeInMs) * 100;
        popup.setProgress(progress);
      } else clearInterval(interval);
    }, 500);
    return interval;
  }

  static createMsgBlock(): HTMLElement {
    const msgBlock = document.createElement('div');
    msgBlock.style.setProperty('height', '100%');
    msgBlock.style.setProperty('width', '40%');
    msgBlock.style.setProperty('position', 'absolute');
    msgBlock.style.setProperty('right', '0px');
    msgBlock.style.setProperty('top', '100%');
    msgBlock.style.setProperty('transform', 'translate(-40%, -120%)');
    return msgBlock;
  }

  // ! The following 3 functions must be in one!
  // ! This is not a very smart 40 lines per function limit I think!
  // ! Now, after splitting into functions, it is much worse readable than before.
  private async drive(
    driveParams: EngineResponse,
    driveId: string,
    flags?: Record<string, unknown>,
  ): Promise<false | number> {
    return new Promise<false | number>((res, rej) => {
      if (this.contract) {
        const { contract } = this;
        const time = GarageDriver.calculateDriveTime(driveParams);
        this.lastStart = Date.now();
        this.lastTimeInMs = time * 1000;
        this.drivePromise = REST_CLIENT_ENGINE.enterDriveMode(
          contract.car.currCar.id,
        ).then(success =>
          this.processSuccessfullDriveReslut(
            success,
            res,
            rej,
            contract,
            driveId,
            time,
            flags,
          ),
        );
        this.controlCarAnim(contract, res, rej, time, flags);
      } else rej(new Error('No car'));
    });
  }

  private controlCarAnim(
    contract: GarageServicemanContract,
    res: (a: number | false) => void,
    rej: (e: unknown) => void,
    time: number,
    flags?: Record<string, unknown>,
  ) {
    contract.car
      .rollForward(time, !flags)
      .catch(() => {
        res(false);
      })
      .then(async playbackRate => {
        if (playbackRate && playbackRate > 0) {
          this.stopCar(true, true, false, flags);
          const connectionRestored = await this.emergencyAwaitServer(
            time * 1000,
            1000,
            // ! wtf unicorn?
            // eslint-disable-next-line unicorn/consistent-destructuring
            this.drivePromise,
          );
          if (!connectionRestored) {
            rej(new Error(REST_CLIENT_ERROR['Failed to fetch']));
          }
        }
      });
  }

  private processSuccessfullDriveReslut(
    success: boolean,
    res: (a: number | false) => void,
    rej: (e: unknown) => void,
    contract: GarageServicemanContract,
    driveId: string,
    time: number,
    flags?: Record<string, unknown>,
  ) {
    if (
      // ! IT IS NECESSARY TO CHECK REMEMBERED CONTRACT WITH CURR CONTRACT AND DRIVE ID
      // ! UNICORN DON'T UNDERSTAND THIS !
      // eslint-disable-next-line unicorn/consistent-destructuring
      !(contract === this.contract) ||
      // eslint-disable-next-line unicorn/consistent-destructuring
      !(driveId === this.currDriveId)
    ) {
      rej(new Error('interrupted'));
    } else if (success) {
      res(time);
      this.stopCar(true, true, false, flags);
    } else {
      this.stopCar(true, false, true, flags);
      res(false);
    }
  }

  // ! 40 lines
  private emergencyAwaitServer = (
    timeInMS: number,
    delayInMS: number,
    resPromise?: Promise<unknown>,
  ) => {
    const popup = new PopupStandaloneModalProgressComp();
    popup.setText(`Connection is lost... awaiting server`).setProgress(1);
    return new Promise<boolean>(res => {
      const startTime = Date.now();
      let IamResolved = false;
      let interval: NodeJS.Timeout;
      const resFunc = () => {
        IamResolved = true;
        if (interval) clearInterval(interval);
        popup.hide();
        res(false);
      };
      const chargeInterval = () => {
        interval = setInterval(() => {
          if (IamResolved) {
            clearInterval(interval);
            return;
          }
          popup.show();
          const currTime = Date.now();
          const elapsedTime = currTime - startTime;
          if (elapsedTime < timeInMS) {
            const progress = (elapsedTime / timeInMS) * 100;
            popup.setProgress(progress);
          } else resFunc();
        }, timeInMS / 20); // 5%
      };

      resPromise?.finally(() => {
        resFunc();
      });
      setTimeout(() => {
        chargeInterval();
      }, delayInMS);
    });
  };

  private async stopCar(
    onlyAnim = false,
    rollover = true,
    broken = false,
    flags?: Record<string, unknown>,
  ) {
    if (this.contract) {
      if (!broken && !onlyAnim) {
        await REST_CLIENT_ENGINE.toggleEngine(
          this.contract.car.currCar.id,
          'stopped',
        );
      }
      await this.contract.car.stop(rollover, broken, flags);
      return true;
    }
    throw new Error('No car');
  }

  private static calculateDriveTime(params: EngineResponse): number {
    const time = params.distance / 1000 / params.velocity; /// !!! distance units???
    return time;
  }
}
