import { DBusInterface } from '../../../../../lib/services/dbus/defs/dbus-interface.def';
import { DBusPort } from '../../../../../lib/services/dbus/defs/dbus-port.def';

export interface CreateUpdateCompControllerReqElems {
  inputText: HTMLInputElement;
  inputColor: HTMLInputElement;
  btn: HTMLButtonElement;
}

export abstract class CreateUpdateCompControllerProto implements DBusPort {
  protected ctlsStateMap = new Map<HTMLElement, boolean>();

  protected ctls = [this.inputText, this.inputColor, this.btn];

  protected disabled = false;

  constructor(
    protected inputText: HTMLInputElement,
    protected inputColor: HTMLInputElement,
    protected btn: HTMLButtonElement,
  ) {}

  abstract autoConnect(int: DBusInterface): void | Promise<void>;

  protected reportValidity(): boolean {
    const { length } = this.inputText.value;
    let valid = true;

    if (length === 0) {
      this.inputText.setCustomValidity('Please, enter a name');
      valid = false;
    }

    if (length > 30) {
      this.inputText.setCustomValidity('Name is too long!');
      valid = false;
    }

    if (!valid) {
      this.inputText.pattern = '';
      this.inputText.reportValidity();
      this.inputText.removeAttribute('pattern');
      return false;
    }

    return true;
  }

  protected toogleCtlsDisabled(state: boolean): void {
    this.inputText.disabled = state;
    this.inputColor.disabled = state;
    this.btn.disabled = state;
  }

  protected pushCtlsState(): void {
    this.ctls.forEach(ctl => {
      this.ctlsStateMap.set(ctl, ctl.disabled);
    });
  }

  protected popCtlsState(): void {
    this.ctls.forEach(ctl => {
      const state = this.ctlsStateMap.get(ctl);
      if (state !== undefined) ctl.disabled = state;
    });
    this.ctlsStateMap.clear();
  }

  disable(): void {
    if (this.disabled) return;
    this.disabled = true;
    this.pushCtlsState();
    this.toogleCtlsDisabled(true);
  }

  enable(): void {
    if (!this.disabled) return;
    this.disabled = false;
    this.popCtlsState();
  }
}
