import { REST_CLIENT_ERROR } from './rest-clients-errors.def';

export abstract class RestClient {
  protected resUrl: string;

  protected fullUrl: string;

  protected baseUrl: string;

  constructor(baseUrl: string, resUrl: string) {
    this.baseUrl = baseUrl;
    this.resUrl = resUrl;
    this.fullUrl = baseUrl + resUrl;
  }

  static handleError(error: Error, rejFunc: (reason?: unknown) => void): void {
    if (error.message in REST_CLIENT_ERROR) {
      const key = error.message as keyof typeof REST_CLIENT_ERROR;
      rejFunc(new Error(REST_CLIENT_ERROR[key]));
    } else rejFunc(new Error(REST_CLIENT_ERROR.UNKNOWN_ERROR));
  }
}
